var axios = require('axios');

var _require = require('../models'),
    InvalidInputError = _require.InvalidInputError;
/**
 *
 * @param {'testnet' | 'mainnet'} network
 */


function getNetworkPath(network) {
  switch (network.toUpperCase()) {
    case 'MAINNET':
      return 'main';

    case 'TESTNET':
      return 'test3';

    default:
      throw InvalidInputError("Unrecognised network ".concat(network));
  }
}
/**
 * push a transaction
 * @param {string} tx raw transaction
 * @param {'testnet' | 'mainnet'} network
 */


function pushtx(tx) {
  var network = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'testnet';
  var path = getNetworkPath(network);
  return axios.post("https://api.blockcypher.com/v1/btc/".concat(path, "/txs/push"), JSON.stringify({
    tx: tx
  }));
}
/**
 * pull all unspent transactions belonging to an address
 * @param {string} address a valid bitcoin address
 * @param {'testnet' | 'mainnet'} network
 */


function pullUnspentTransactions(address) {
  var network = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'testnet';
  var path = getNetworkPath(network);
  return axios.get("https://api.blockcypher.com/v1/btc/".concat(path, "/addrs/").concat(address, "?unspentOnly=true")).then(function (response) {
    var txs = Array.isArray(response.data.txrefs) ? response.data.txrefs : [response.data.txrefs];

    if (!txs[0]) {
      return null;
    }

    var txMap = [];
    txs.forEach(function (tx) {
      if (!txMap.includes(tx.tx_hash) && tx.confirmations >= 6) {
        txMap.push(tx.tx_hash);
      }
    });

    if (!txMap.length) {
      return null;
    }

    var txParam = txMap.join(';');
    return axios.get("https://api.blockcypher.com/v1/btc/".concat(path, "/txs/").concat(txParam));
  });
}

module.exports = {
  pushtx: pushtx,
  pullUnspentTransactions: pullUnspentTransactions
};